<template>
  <div>
    <v-app>
      <h1>제목: {{ getDict($route.params.id).title }} </h1>
      <h2>작가: {{getDict($route.params.id).author}} </h2>
      </br>
      </br>
      <h2> {{ getDict($route.params.id).contents }} </h2>
    </v-app>
  </div>
</template>



<script>
import testData from '../assets/testData.js'
export default {
  name: 'poems',
  data() {
    return {
      poem_contents: [],
      listData: []
    }
  },
  created(){
    this.listData=testData
  },
  methods: {
    getDict(id){ for(let i of this.listData){ if(i.id == id) return i;} }
  }
}
</script>
